import {atomNames} from "./mydata";
import {atomObjects, Atom} from "./mydata";

const divTarg1 = document.getElementById("part1");

const orderedList1 = document.createElement("listSection");
orderedList1?.setAttribute("id","orderedList");

var ol1 = document.createElement("ol");

divTarg1?.appendChild(orderedList1);
orderedList1.appendChild(ol1);

for(var i = 0; i < atomNames.length;i++){
    var li = document.createElement("li");
    ol1.appendChild(li);
    li.innerHTML = li.innerHTML + atomNames[i];
}

const divTarg2 = document.getElementById("part2");
const orderedList2 = document.createElement("listSection");
orderedList2?.setAttribute("id","orderedList");

var ol2 = document.createElement("ol");

divTarg2?.appendChild(orderedList2);
orderedList2.appendChild(ol2);

for(var i = 0; i < atomObjects.length;i++){
    var li = document.createElement("li");
    ol2.appendChild(li);
    
    li.innerHTML = li.innerHTML + atomObjects[i].name + 
    "(weight: " + atomObjects[i].weight + ")";
    if(atomObjects[i].weight>150){
        li.classList.add("heavy")
    }else{
        li.classList.add("light");
    }
}

const divTarg3 = document.getElementById("part3");
const tableSection = document.createElement("tableSection");
const Table = document.createElement("TABLE");
Table?.setAttribute("id","table");
Table.style.width = "100%";
Table.setAttribute('border', '1');
const tableHead = document.createElement("thead");
const tableBody = document.createElement('tbody');
Table.appendChild(tableHead)
var tableRow = document.createElement("tr");
var tableNameCell = document.createElement("th");
tableNameCell.appendChild(document.createTextNode("Atom name"));
tableRow.appendChild(tableNameCell);
var tableWeightCell = document.createElement("th");
tableWeightCell.appendChild(document.createTextNode("Weight"));
tableRow.appendChild(tableWeightCell);
tableHead.appendChild(tableRow);
Table.appendChild(tableBody);
divTarg3?.appendChild(tableSection);
tableSection.appendChild(Table);

for(var i = 0;i<atomNames.length;i++){
    var innerTableRow = document.createElement('tr');
    for(var j = 0; j < 1;j++){
        var nameCell = document.createElement('td');
        var weightCell = document.createElement('td');
        nameCell.appendChild(document.createTextNode(atomObjects[i].name));
        weightCell.appendChild(document.createTextNode
            (String(atomObjects[i].weight)));
        innerTableRow.appendChild(nameCell);
        innerTableRow.appendChild(weightCell)
        if(atomObjects[i].weight > 150){
            innerTableRow.classList.add("heavy")
        }else{
            innerTableRow.classList.add("light")
        }
    }
    tableBody.appendChild(innerTableRow);
}